import StateBtn from './state-button.vue'

//
const vueStateBtn = {
  install(Vue, options) {
    Vue.component(StateBtn.name, StateBtn)
  }
}

/*  */
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(vueStateBtn);
}

export default vueStateBtn


